@charset "UTF-8";
:root {
  --font-family: "Open Sans", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable value-keyword-case */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.main-title {
  font-weight: 300;
  font-size: 40px;
  line-height: 50px;
  color: #FFFFFF;
  margin: 0;
  text-transform: uppercase;
}

.header {
  background-color: #000000;
}

/* МЕНЮ */
.header__bottom {
  background: rgba(255, 255, 255, 0.06);
}

.menu {
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  padding: 27px 0;
  gap: 26px;
}

.menu-item a {
  color: #FFFFFF;
  font-family: "CeraPro-Reg";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 19px;
  color: #FFFFFF;
  transition: color 0.3s ease-in-out;
}

.menu-item a:hover {
  color: #E6D5BD;
}

.current_page_item a {
  color: #E6D5BD;
}

.menu-item-type-custom a {
  color: #FFFFFF !important;
}

.menu-item-object-custom a:hover {
  color: #E6D5BD !important;
}

.menu li {
  margin-right: 24px;
}

.menu li:last-child {
  margin-right: 0;
}

.rightside-menu__close {
  display: none;
}

.header__btn {
  display: none;
}

.current-menu-item {
  text-shadow: 0 0 0.2px #1F1D1D;
}

.nav {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 100px;
}

/* АДАПТИВ МЕНЮ */
@media (max-width: 992px) {
  .menu {
    margin: 0;
    padding: 40px 30px 10px 30px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .menu li {
    padding-bottom: 15px;
  }
  .menu li a:hover {
    color: #121212;
  }
  .rightside-menu__close {
    display: block;
    border: none;
    background-color: transparent;
    width: 40px;
    height: 40px;
    cursor: pointer;
    position: absolute;
    top: 5px;
    right: 5px;
  }
  .rightside-menu__close-span {
    background-color: #ffffff;
    display: block;
    width: 30px;
    opacity: 1;
    right: 5px;
    transform: rotate(0deg);
    height: 2px;
    position: absolute;
  }
  .rightside-menu__close-span:nth-child(1) {
    top: 20px;
    transform: rotate(45deg);
  }
  .rightside-menu__close-span:nth-child(2) {
    top: 20px;
    transform: rotate(-45deg);
  }
  .header__btn {
    display: block;
    position: absolute;
    width: 28px;
    height: 20px;
    left: 0;
    transform: rotate(0deg);
    transition: 0.5s ease-in-out;
    cursor: pointer;
    z-index: 999;
    left: auto;
    left: 15px;
    top: 15px;
    background-color: transparent;
    border: none;
  }
  .rightside-menu {
    z-index: 5;
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    max-width: 694px;
    background-color: #fff;
    padding-top: 40px;
    width: 100%;
    transition: all 0.5s;
  }
  .rightside-menu--close {
    transform: translateX(500%);
  }
  .header__bottom .nav {
    position: fixed;
    background-color: #EFC97E;
    transform: translateX(0);
    transition: all 0.5s;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 9999;
  }
  .header__bottom .nav--close {
    transform: translateX(-100%);
  }
  .header__btn-span {
    background-color: #ffffff;
    display: block;
    position: absolute;
    width: 100%;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: 0.25s ease-in-out;
    height: 3px;
  }
  .header__btn-span:nth-child(1) {
    top: 0;
  }
  .header__btn-span:nth-child(2) {
    top: 8px;
  }
  .header__btn-span:nth-child(3) {
    top: 16px;
  }
}
@media (max-width: 768px) {
  .menu li a {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 400px) {
  .menu li a {
    font-size: 12px;
    line-height: 15px;
  }
}
.nav-wrap {
  display: flex;
  justify-content: space-between;
}

.main-hero {
  background-image: url(../img/main-hero.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.main-hero__title {
  padding-top: 166px;
}
.main-hero__btn {
  margin-top: 32px;
  margin-bottom: 247px;
  border: none;
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.26);
  backdrop-filter: blur(2.5px);
  border-radius: 279px;
  color: #FFFFFF;
  padding: 16px 64px;
  cursor: pointer;
}

.section-figure {
  width: 100%;
  height: 70px;
  border-radius: 100% 100% 0 0;
  background-color: #FFFFFF;
  margin-top: -70px;
  position: relative;
}

.main-descr {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  padding-bottom: 80px;
}
.main-descr__wrap {
  position: relative;
}
.main-descr__wrap:before {
  content: "";
  position: absolute;
  top: -5px;
  left: -10px;
  width: 18px;
  height: 18px;
  border-left: 2px solid #88E4AE;
  border-top: 2px solid #88E4AE;
  border-radius: 4px 0 0;
}
.main-descr__wrap:after {
  content: "";
  position: absolute;
  bottom: -5px;
  right: -10px;
  width: 18px;
  height: 18px;
  border-right: 2px solid #88E4AE;
  border-bottom: 2px solid #88E4AE;
  border-radius: 0 0 4px;
}
.main-descr__text {
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 144%;
  text-align: center;
  color: #000000;
  margin: 0;
  padding-bottom: 24px;
  max-width: 611px;
}

.menu-block {
  position: sticky;
  top: 25px;
}

.our-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
  gap: 15px;
  max-width: 626px;
  margin: 0 auto;
}
.our-menu a {
  background: #E5D7BE;
  box-shadow: inset 4px 4px 6px rgba(255, 255, 255, 0.34);
  border-radius: 30px;
  padding: 11px 26px;
  color: #000000 !important;
}
.our-menu li {
  margin-bottom: 15px;
}

.columns-4 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 32px;
}

.products-block__title {
  padding-top: 120px;
  text-align: center;
}

.products-block .products {
  list-style: none;
  padding: 0;
}